import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // api: "https://rusa-json-server.herokuapp.com/programs",
    api: "http://localhost:3200/programs",
    api_user: "http://localhost:5000/api/v1/users",
    // api_user: "http://100.21.100.104:5000/api/v1/users",
    // "http://localhost:5000/api/v1/users",
    // api:"http://localhost:3200/programs",
    id: 0,
    subid: 0,
    greet: "Hello there",
    fetched: false,
    data: [],
  },
  mutations: {
    fetchData(state, data) {
      data.forEach((element) => {
        state.data.push(element);
      });
      // fetched
      state.fetched = true;
      // console.log(state.data)
    },
  },
  actions: {
    async fetchData(context) {
      let raw = await fetch(context.state.api);
      let data = await raw.json();
      context.commit("fetchData", data);
      //- DEBUG
      // console.log(json)
    },
  },
  modules: {},
});
