import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";

Vue.use(Router);

export default new Router({
  // mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
    },
    {
      path: "/about",
      name: "about",
      component: () => import("./views/About.vue"),
    },
    {
      path: "/contact",
      name: "contact",
      component: () => import("./views/Contact.vue"),
    },
    {
      path: "/courses",
      name: "courses",
      component: () => import("./views/courses.vue"),
    },
    {
      path: "/programs/:program/:id/:subid",
      name: "subprogram",
      component: () => import("./views/Subprogram.vue"),
      props: true,
    },
    {
      path: "/centres/:program/:id",
      name: "programs",
      component: () => import("./views/Program.vue"),
      // props: true,
    },
    {
      path: "/programs/:program/:id/:subid/register",
      name: "register",
      component: () => import("./views/Register.vue"),
      props: true,
    },
    {
      path: "/form",
      name: "form",
      component: () => import("./views/Register.vue"),
    },
    {
      path: "/admin",
      name: "admin",
      component: () => import("./views/Admin.vue"),
      props: true,
    },
    {
      path: "/program/form1",
      name: "form1",
      component: () => import("./views/Form.vue"),
      props: true,
    },
    {
      path: "*",
      name: "error",
      component: () => import("./views/PageNotFound.vue"),
    },
    // ,
    //  {
    //     path: "/home2",
    //     name: "home2",
    //     component: () => import("./views/Home.vue"),
    //     redirect: '/form'
    // }
  ],
});
